import Vue from 'vue'

const state = {
  cart: [],
  statusMiniCart: false
}

const getters = {
  getItemsCart: state => state.cart,
  getStatusMiniCart: state => state.statusMiniCart
}

const actions = {
  addToCart: function ({commit, dispatch}, { superSku, sku, count }) {
    let p = this.state.cart.cart.find(p => p.superSku === superSku)
    let f = this.state.cart.cart.find(p => p.activeSimple === sku)
    if (p && f) {
      commit('CART_INCREMENT', {superSku, sku, count: f.count = f.count + count})
    } else {
      commit('ADD_TO_CART', { superSku, sku, count })
    }
    dispatch('setLocalStorage', {superSku, sku, count})
    // dispatch(PUSH, {name: 'cart', id: superSku})
  },
  switchStatus: function ({commit}, value) {
    commit('SWITCH_STATUS', value)
  },
  deleteItemFromCart: function ({commit}, value) {
    let i = state.cart.findIndex(p => p.superSku === value.superSku)
    let ls = JSON.parse(Vue.localStorage.get('Cart'))
    ls.splice(i, 1)
    let index = this.state.cart.cart.indexOf(value)
    this.state.cart.cart.splice(index, 1)
    commit('DELETE_ITEM_FROM_CART', ls)
  },
  cartClear: function ({commit}) {
    commit('CART_CLEAR')
  },
  changeItemCount: function ({commit, dispatch}, {product, sku, count}) {
    commit('CHANGE_ITEM_COUNT', {product, sku, count})
    dispatch('setLocalStorage', {superSku: product.superSku, count, sku})
  },
  setLocalStorage: function ({commit}, {superSku, sku, count}) {
    let p = this.state.cart.cart.find(p => p.activeSimple === sku)
    let ls = JSON.parse(Vue.localStorage.get('Cart'))
    if (ls !== null) {
      let lsFind = ls.findIndex(f => f.sku === sku)
      if (lsFind === -1) {
        ls.push({superSku, sku, count})
        Vue.localStorage.set('Cart', JSON.stringify(ls))
      } else {
        ls.splice(lsFind, 1, { superSku, sku, count: p.count })
        Vue.localStorage.set('Cart', JSON.stringify(ls))
      }
    } else {
      Vue.localStorage.set('Cart', JSON.stringify([{superSku, sku, count}]))
    }
  }
}

const mutations = {
  ADD_TO_CART: function (state, { superSku, sku, count }) {
    let product = this.state.products.find(p => p.superSku === superSku)
    if (product) {
      let obj = Object.assign(JSON.parse(JSON.stringify(product)), {count})
      state.cart.push(obj)
    }
  },
  SWITCH_STATUS: function (state, value) {
    state.statusMiniCart = value
  },
  DELETE_ITEM_FROM_CART: function (state, ls) {
    Vue.localStorage.set('Cart', JSON.stringify(ls))
  },
  CART_CLEAR: function (state) {
    state.cart = []
    Vue.localStorage.remove('Cart')
  },
  CHANGE_ITEM_COUNT: function (state, {product, count, sku, ls}) {
    let p = state.cart.find(p => p.activeSimple === product.activeSimple)
    let i = state.cart.findIndex(p => p.activeSimple === product.activeSimple)
    if (p) {
      state.cart.splice(i, 1, Object.assign(p, {count}))
    }
  },
  CART_INCREMENT: function (state, {superSku, sku, count}) {
    let product = this.state.products.find(p => p.activeSimple === sku)
    let i = state.cart.findIndex(p => p.activeSimple === sku)
    state.cart.splice(i, 1, Object.assign(JSON.parse(JSON.stringify(product)), {count}))
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



