const getters = {
  getProduct: state => num => state.products[num],
  getContacts: state => state.mainInfo.contacts,
  getProductBySku: state => superSku => state.products.find(p => p.superSku === superSku),
  getProducts: state => state.products,
  getStatusSend: state => state.statusSend,
  getSimpleProductsKeys: state => (num, key) => state.products[num].simpleProducts[key]
}
const actions = {
  addProducts: function ({commit}, product) {
    commit('ADD_PRODUCTS', product)
  },
  addMain: function ({commit}, main) {
    commit('ADD_MAIN', main)
  },
  sendStatus: function ({commit}, {status}) {
    commit('SEND_STATUS', {status})
  },
  init: function ({commit, dispatch}) {
    let lsCustomer = Vue.localStorage.get('customer')
    let lsOrder = Vue.localStorage.get('order')
    let lsUser = Vue.localStorage.get('user')
    if (lsCustomer !== null) {
      let obj = JSON.parse(lsCustomer)
      this.dispatch('changeCustomer', {type: 'customer', value: obj})
    }
    if (lsOrder !== null) {
      let obj = JSON.parse(lsOrder)
      this.dispatch('changeCustomer', {type: 'order', value: obj})
    }
    if (lsUser !== null) {
      let obj = JSON.parse(lsUser)
      this.dispatch('changeCustomer', {type: 'user', value: obj})
    }
  },
  initCart: function ({commit}) {
    let lsCart = Vue.localStorage.get('Cart')
    if (lsCart !== null) {
      let arr = JSON.parse(lsCart)
      let length = arr.length
      for (let i = 0; i < length; i++) {
        commit('ADD_TO_CART_FROM_LS', {item: arr[i]})
      }
    }
  },
  changeProduct: function ({commit}, {product, sku}) {
    commit('CHANGE_PRODUCT', {product, sku})
  },
  addArticleProducts: function ({commit}, {product}) {
    commit('ADD_ARTICLE_PRODUCTS', {product})
  }
}
const mutations = {
  ADD_MAIN: function (state, main) {
    state.mainInfo = main
  },
  ADD_PRODUCTS: function (state, product) {
    state.products = product
  },
  SEND_STATUS: function (state, {status}) {
    state.statusSend = {status}
  },
  ADD_TO_CART_FROM_LS: function (state, {item}) {
    let find = state.products.find(p => p.superSku === item.superSku)
    if (find) {
      state.cart.cart.push(Object.assign(JSON.parse(JSON.stringify(find)), {count: item.count}, {activeSimple: item.sku}))
    }
  },
  CHANGE_PRODUCT: function (state, {product, sku}) {
    let p = state.products.find(p => p.superSku === product.superSku)
    let i = state.products.findIndex(p => p.superSku === product.superSku)
    state.products[i].activeSimple = sku
    p.activeSimple = sku
  },
  ADD_ARTICLE_PRODUCTS: function (state, {product}) {
    this.state.products = product
  }
}

const store = new Vuex.Store({
  modules: {
    cart: Cart,
    blog: Blog,
    customer: Customer
  },
  state,
  getters,
  actions,
  mutations,
  localizeRus
})
Vue.use(vuexI18n.plugin, store)
Vue.i18n.add('ru', localizeRus)
Vue.i18n.add('en', localizeEng)
Vue.i18n.add('ua', localizeUa)
Vue.i18n.set('ru')

export default store



