<template>
  <modal name="cart">
    <div class="product-modal-container dialog-content">
      <div class="product-modal" v-for="(item, index) in itemsCart">
        <div>
          <img :src="item.simpleProducts[item.activeSimple].images[0].src" alt="">
        </div>
        {{item.title}}
        <div class="amount-product">
          <strong>
            <a href="#" @click.prevent="productItemMinus(index)">-</a>
            {{item.count}}{{ $t('buttons.count')}}
            <a @click.prevent="productItemPlus(index)" href="#">+</a>
          </strong>
          <div><strong
            :class="{active: item.simpleProducts[item.activeSimple].specialPrice}">
            {{ item.simpleProducts[item.activeSimple].price }}
            {{ $t('buttons.price')}}
          </strong>
            <strong
              v-if="item.simpleProducts[item.activeSimple].specialPrice">
              {{ item.simpleProducts[item.activeSimple].specialPrice }}
              {{ $t('buttons.price')}}
            </strong></div>
          <a href="#" @click.prevent="deleteItem(index)" class="trash"><icon name="trash"></icon></a>
        </div>
      </div>
    </div>

    <div class="product-modal-total">{{ $t('form.summ')}} {{cartSum}} {{ $t('buttons.price')}}</div>
    <div class="modal-buttons">
      <button class="button" type="button" @click="close">{{ $t('buttons.continueShopping')}}</button>
      <router-link to="/order" class="button" > <span @click="close">{{ $t('buttons.order')}}</span></router-link>
    </div>
  </modal>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Icon from 'vue-awesome/components/Icon'

  export default {
    name: 'modalCart',
    data () {
      return {
      }
    },
    computed: {
      ...mapGetters({
        itemsCart: 'getItemsCart',
        getProduct: 'getProduct'
      }),
      cartSum () {
        let arr = this.itemsCart
        let count = 0
        let price = 0
        let sum = 0
        for (let i = 0; i < arr.length; i++) {
          count = arr[i].count
          if (arr[i].simpleProducts[arr[i].activeSimple].specialPrice) {
            price = arr[i].simpleProducts[arr[i].activeSimple].specialPrice
          } else {
            price = arr[i].simpleProducts[arr[i].activeSimple].price
          }
          sum += count * price
        }
        return sum
      }
    },
    components: {
      Icon
    },
    methods: {
      deleteItem (index) {
        this.$store.dispatch('deleteItemFromCart', this.itemsCart[index], index)
      },
      close () {
        this.$modal.hide('cart')
      },
      productItemPlus (index) {
        let count = this.itemsCart[index].count
        count += 1
        if (count < 1) {
          count = 1
        }
        this.$store.dispatch('changeItemCount', {product: this.itemsCart[index], sku: this.itemsCart[index].activeSimple, count: count})
      },
      productItemMinus (index) {
        let count = this.itemsCart[index].count
        count -= 1
        if (count < 1) {
          count = 1
        }
        this.$store.dispatch('changeItemCount', {product: this.itemsCart[index], sku: this.itemsCart[index].activeSimple, count: count})
      }
    }
  }
</script>

<style lang="scss">
  .product-modal{
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
      width:255px;
      height: 179px;
    }
  }
  .amount-product{
    display: flex;
    strong{
      margin: 0 10px 0 0;
    }
    a{
      margin: 0 5px;
    }
    .trash{
      display: flex;
      align-items: center;
    }
  }
  .modal-buttons{
    display: flex;
    justify-content: center;
    .button{
      margin: 0 5px;
    }
  }
  [data-modal="cart"] .v--modal{
    text-align: center;
    height: 400px !important;
  }
  .product-modal-container{
    padding: 1rem;
    overflow: auto;
    max-height: 300px;
  }
  .columns:not(.is-desktop){
    justify-content: center;
  }
  .modal-buttons{
    padding: 1rem;
  }
  .product-modal-total{
    padding: 1rem 0 0 0;
    border-top: 2px solid #efefef;
  }
</style>



