<template>
  <div>
      <div class="cart-title" @click.prevent="openMiniCart">
        <icon class="fa-cart" name="shopping-bag"></icon>
        <!-- {{ $t('form.cart')}} - {{cartCount}} {{ $t('buttons.count')}} -->
        <span class="cart-count">{{cartCount}}</span>
      </div>
      <div class="cart" v-show="statusMiniCart">
        <a @click.prevent="openMiniCart" class="cart-close"></a>
        <div class="box">
          <template v-if="itemsCart.length > 0">
            <div class="cart-list">
              <div v-for="(item, index) in itemsCart" :key="index" class="media">
                <div class="media-left">
                  <div class="image is-64x64">
                    <img :src="item.simpleProducts[item.activeSimple].images[0].src" alt="">
                  </div>
                </div>
                <div class="media-content">
                  <div class="content">
                    <div class="cart-list-name has-text-weight-bold">{{item.simpleProducts[item.activeSimple].title}}</div>
                    <div class="cart-list-count">{{ $t('form.count')}}: {{item.count}} {{ $t('buttons.count')}}</div>
                    <div>
                      <span class="cart-list-price">
                        <template v-if="item.simpleProducts[item.activeSimple].specialPrice">
                          {{item.simpleProducts[item.activeSimple].specialPrice}} {{ $t('buttons.price')}}
                        </template>
                        <template v-else>
                          {{item.simpleProducts[item.activeSimple].price}} {{ $t('buttons.price')}}
                        </template>
                      </span>
                      <!-- <a @click.prevent="deleteItem(index)">{{ $t('buttons.delete')}}</a> -->
                      <a @click.prevent="deleteItem(index)" class="cart-list-del"><icon name="trash"></icon></a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="cart-bottom">
              <div class="cart-summ">{{ $t('form.summ')}}: {{cartSum}} {{ $t('buttons.price')}}</div>
              <router-link to="/order" class="button is-primary is-small"> {{ $t('buttons.order')}}</router-link>
            </div>
          </template>
          <template v-else>
            <strong>Ваша корзина пуста</strong>
          </template>
        </div>
      </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/shopping-bag'
  import 'vue-awesome/icons/trash'

  export default {
    name: 'Cart',
    data () {
      return {
      }
    },
    components: {
      Icon
    },
    methods: {
      openMiniCart () {
        let value
        this.statusMiniCart === false ? value = true : value = false
        this.$store.dispatch('switchStatus', value)
      },
      deleteItem (index) {
        this.$store.dispatch('deleteItemFromCart', this.itemsCart[index], index)
      }
    },
    computed: {
      ...mapGetters({
        product: 'getProduct',
        itemsCart: 'getItemsCart',
        statusMiniCart: 'getStatusMiniCart'
      }),
      cartCount () {
        let arr = this.itemsCart
        let count = 0
        for (let i = 0; i < arr.length; i++) {
          count += arr[i].count
        }
        return count
      },
      cartSum () {
        let arr = this.itemsCart
        let count = 0
        let price = 0
        let sum = 0
        for (let i = 0; i < arr.length; i++) {
          count = arr[i].count
          if (arr[i].simpleProducts[arr[i].activeSimple].specialPrice) {
            price = arr[i].simpleProducts[arr[i].activeSimple].specialPrice
          } else {
            price = arr[i].simpleProducts[arr[i].activeSimple].price
          }
          sum += count * price
        }
        return sum
      }
    }
}
</script>

<style lang="scss">
  .cart {
    position: absolute;
    z-index: 10;
    color: #000;
    right: 15px;
    top: calc(100% + 10px);
    width: 280px;
    &-close{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      cursor: default;
      // background: rgba(#000, 0.2);
    }
    .box{padding: 0.5rem;}
    &-title{
      position: relative;
      cursor: pointer;
    }
    &-count{
      position: absolute;
        top: 16px;
      left: 50%;
      transform: translateX(-50%);
      color: #1c242d;
      font-size: 0.8rem;
    }
    &-list{
      max-height: 160px;
      overflow-y: auto;
      border-bottom: 1px solid #dbdbdb;
      padding-bottom: 1rem;
      margin: 0 0 0.5rem;
      &-name{font-size: 0.9rem;}
      &-count{font-size: 0.8rem;}
      &-price{
        font-size: 0.8rem;
        display: inline-block;
        vertical-align: middle;
        }
      &-del{
        display: inline-block;
        vertical-align: middle;
        line-height: 1;
        margin: 0 0 0 10px;
      }
    }
    &-bottom{
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-summ{
      font-size: 0.9rem;
    }
  }
  .fa-cart {
    height: 43px;
    display: block;
    color: #fff;
  }
</style>



