<template>
  <div>
    <img
      class="subitem"
      @click.prevent="changeProduct(product, sku)"
      :src="product.simpleProducts[sku].images[0].src"
      :alt="product.simpleProducts[sku].images[0].alt">
  </div>

</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'subitem',
    computed: {
      ...mapGetters({
        products: 'getProducts',
        simpleKeys: 'getSimpleProductsKeys'
      })
    },
    methods: {
      changeProduct (product, sku) {
        this.$store.dispatch('changeProduct', {product, sku})
      }
    },
    props: {
      product: { type: Object, require: true },
      sku: { type: String, require: true }
    }
  }
</script>

<style lang="scss">

  .image img{
    max-width: 205px;
    max-height: 129px;
    margin: auto;
  }
  .subitem{
    height: 50px;
    width: 60px;
  }
  .basic-modal{
    text-align: center;
    form{
      width: 300px;
      margin: auto;
    }
  }
</style>



