<template>
  <div id="app">
    <header-component></header-component>
    <main class="main">
      <router-view></router-view>
    </main>
    <footer-component></footer-component>
    <modal-cart></modal-cart>
    <modal-question></modal-question>
  </div>
</template>

<script>
  import HeaderComponent from '@/components/layout/header'
  import FooterComponent from '@/components/layout/footer'
  import ModalQuestion from '@/components/View/modalQuestion'
  import ModalCart from '@/components/View/modalCart'
  import ApiMain from '@/api/main'
  import { mapGetters } from 'vuex'

  export default {
    name: 'app',
    components: {
      HeaderComponent,
      FooterComponent,
      ModalCart,
      ModalQuestion
    },
    methods: {
    },
    computed: {
      ...mapGetters({
        products: 'getProducts',
        mainInfo: 'getMainInfo'
      })
    },
    created () {
      ApiMain.getProducts()
      .then(products => {
        this.$store.dispatch('addProducts', products.data)
        this.$store.dispatch('initCart')
      })
      .catch(errors => {
        alert(errors)
      })
      this.$store.dispatch('init')

      ApiMain.getMain()
      .then(response => {
        this.$store.dispatch('addMain', response.data)
      })
      .catch(errors => {
        alert(errors)
      })
    }
  }
</script>

<style lang="scss">
  @import '~bulma';
  @import 'assets/variable.scss';
  body {
    margin: 0;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    padding-top: $headerHight;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

  .main {
    text-align: center;
    flex: 1 1 auto;
    padding-bottom: 100px;
  }
  .container{
    padding: 0 15px;
  }
  a{
    &[href^=mailto], &[href^=tel], &[href^=skype], &[href^=sms] {
      color: inherit !important;
      text-decoration: none !important;
      display: inline !important;
      padding: 0 !important;
      border: 0 !important;
      cursor: default !important;
      pointer-events: none;
    }
  }
  .title{
    font-weight: 300;
    &.is-2{
    padding-top: 50px;
  }
  }
  .blogItems article .image a::before{
    background: url('assets/link.png') center no-repeat;
  }
  .breadcrumb{
    background-color: transparent;
  }
  .fa-icon {
    width: auto;
    max-width: 100%;
    max-height: 100%;
  }
  .select select,{
    width: 220px;
  }
  input{
    width: 100%;
  }
  .columns{
    margin:0 !important;
  }
  button.is-success {
    margin: 1rem 0 0 0;
  }
  .select{
    width:100%;
  }
  .select select {
    width: 100%;
  }
  .blogItems {
    display: flex;
    flex-wrap: wrap;
  }
  .level-right {
    margin: .5rem 0;
  }
  .description{
    max-width: 240px;
    margin: auto;
    font-size: 15px;
  }

</style>



