import axios from 'axios'
export default {
  getBlog: (num, id) => {
    // return axios.get('/blog?id=1&id=2')
    return axios.get(`/blog?_limit=${num}&_page=${id}&_sort=id&_order=DESC`)
  },
  getPost: (id) => {
    return axios.get(`/blog/${id}`)
  },
  getPostProducts: () => {
    return axios.get('/products')
  },
  getPostBySku: (sku) => {
    console.log(sku)
    // если в productsOfArticle один элемент, то массив возвращается, в другом случае нет :(
    return axios.get(`/blog?productsOfArticle=${sku}`)
  }
}



