<template>
  <article class="product">
    <div class="column" @mouseover="showProduct" @mouseout="hideProduct">
      <h2>
        <strong>{{ product.title }}</strong>
      </h2>

      <div class="image">
        <img
          :src="product.simpleProducts[product.activeSimple].images[0].src"
          :alt="product.simpleProducts[product.activeSimple].images.alt"
          :title="product.simpleProducts[product.activeSimple].images.title">
      </div>
      <div class="subitem-product">
        <subitem
        v-for="(item, index, value) in product.simpleProducts"
        :key="index"
        :product="product"
        :id="num"
        :sku="index">
      </subitem>

      </div>

      <p>{{ product.simpleProducts[product.activeSimple].title }}</p>
      <div class="price">
        <div>Цена:</div>
        <strong
          :class="{active: product.simpleProducts[product.activeSimple].specialPrice}">
          {{ product.simpleProducts[product.activeSimple].price }}
          {{ $t('buttons.price')}}
        </strong>
        <strong
          v-if="product.simpleProducts[product.activeSimple].specialPrice">
          {{ product.simpleProducts[product.activeSimple].specialPrice }}
          {{ $t('buttons.price')}}
        </strong>
      </div>
      <a @click.prevent="openQuestion(product)" class="button is-warning">
        <icon name="question"></icon>
      </a>
      <router-link :to="{name: 'articlesProduct', params: {id: 'product' + '_' + product.superSku}}" class="button is-info">
        <icon name="newspaper-o"></icon>
      </router-link>
      <transition name="fade">
      <div v-show="show" class="hidden-hover">

        <div>
          <div class="description">
            <div>
              <strong>
                Описание:
              </strong>
              <br />
              <span class="text-description">{{ product.description }}</span>
            </div>
            <div v-for="(attr, index) in product.simpleProducts[product.activeSimple].attributes" :key="index">
              <div>
                <strong>
                  Атрибуты:
                </strong>
              </div>
              <span>{{ attr.title }} - {{ attr.value }}</span>
            </div>
          </div>

        </div>
      <div class="column-price">
        <div class="level-right buy-container">

          <div class="level-right">
            <input class="input" style="text-align: center" min="1" type="number" v-model.number="value">
          </div>

        </div>
      </div>
      <a class="button is-danger" @click.prevent="sendToCart(num)">
        {{ $t('buttons.buy')}}
      </a>
    </div>
      </transition>
    </div>

  </article>
</template>

<script>
  import { mapGetters } from 'vuex'
  import subitem from '@/components/View/subitem'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/question'
  import 'vue-awesome/icons/newspaper-o'

  export default {
    name: 'Product',
    data () {
      return {
        value: 1,
        show: false
      }
    },
    components: {
      subitem,
      Icon
    },
    props: {
      product: { type: Object, require: true },
      num: { type: Number, require: true }
    },
    computed: {
      ...mapGetters({
        products: 'getProducts'
      })
    },
    methods: {
      sendToCart (num) {
        let product = {
          count: this.value,
          sku: this.product.activeSimple,
          superSku: this.product.superSku
        }
        this.$store.dispatch('addToCart', product)
        this.$modal.show('cart', {name: 'cart', id: product.superSku})
      },
      openQuestion (product) {
        this.$modal.show('question', {name: 'question', id: product})
      },
      showProduct () {
        this.show = true
      },
      hideProduct () {
        this.show = false
      }
    }
  }

</script>
<style lang="scss">
  .active{
    text-decoration: line-through;
  }
  .price{
    display: flex;
    justify-content: center;
    div, strong{
      margin: 0 3px;
    }
  }
  .product{
    width:calc(33.33333% - 2.8rem);
    border-right: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    border-left:1px solid #eaeaea;
    box-shadow: 1px 1px 1px 1px #e6e6e6;
    margin: 1.4rem;
    padding: 2rem 0 .7rem;
    display: flex;
    flex-direction: column;
    position: relative;
    align-self: flex-start;
    &>.column{
      flex-grow: 0;
      display: flex;
      flex-direction: column;
      flex-basis: initial;
    }
  }
  .column-price{
    flex-direction: column;
  }
  .buy-container{
    flex-direction: column;
  }
  .level-left{
    margin: auto;
    justify-content: center;
  }
  .colors-column{
    flex-direction: column;
  }
  .column-variation{
    flex-wrap: wrap;
    img{
      cursor: pointer;
    }
    &>div{
      margin: .1rem;
    }
  }
  .product{
    .button.is-warning{
      position: absolute;
      top:10px;
      right: 5px;
    }
    .button.is-info {
      position: absolute;
      top:10px;
      right: 45px;
    }
  }
  .hidden-hover{
    text-align: left;
    position: absolute;
    background: #fff;
    z-index: 10;
    width: 100.5%;
    left:-1px;
    border-right: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    border-left: 1px solid #eaeaea;
    box-shadow: 1px 2px 1px 1px #e6e6e6;
    padding: .75rem;
    top: 330px;
    .button.is-danger{
     display: block;
    }
  }
  .text-description{
    text-align: left;
    display: block;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
    opacity: 0
  }
  .subitem-product{
    display: flex;
    justify-content: center;
    margin: 1rem;
  }
</style>



