<template>
  <header class="header">
    <ul class="nav">
      <li><router-link to="/">Главная</router-link></li>
      <li><router-link to="/blog">Блог</router-link></li>
    </ul>
    <div class="is-flex">
      <ul class="header-contacts" v-if="contacts">
        <li v-for="(phone, index) in contacts.phones" :key="index" class="is-flex">
          <span><icon class="fa-phone" :name="phone.icon"></icon></span>
          <a :href="phone.href">{{ phone.view }}</a>
        </li>
      </ul>
      <cart></cart>
    </div>
  </header>
</template>
<script>
  import Cart from '@/components/View/Cart'
  import { mapGetters } from 'vuex'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/mobile-phone'
  import 'vue-awesome/icons/phone'
  export default {
    data () {
      return {
      }
    },
    components: {
      Cart,
      Icon
    },
    computed: {
      ...mapGetters({
        contacts: 'getContacts'
      })
    }
  }
</script>
<style lang="scss" scoped>
  @import '../../assets/variable.scss';
  .header {
    margin: 0;
    height: $headerHight;
    padding: 0 15px;
    background: $headerColor;
    color: $color0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    z-index: 10;
    .router-link-exact-active{display: none;}
    &-contacts{
      font-size: 0.8rem;
      margin: 0 20px 0 0;
      display: flex;
      justify-content: center;
      flex-direction: column;
      span{
        display: inline-block;
        vertical-align: middle;
        width: 20px;
        text-align: center;
      }
    }
  }
  .nav{
    display: flex;
    align-items: center;
    li{
      display: inline-block;
      vertical-align: top;
      margin: 0 20px 0 0;
    }
    a{
      color: #fff;
      font-size: 1rem;
    }
  }
  .fa-phone{
    height: 18px;
  }
</style>



