<template>
  <footer class="footer">
    <a href="javascript:void(0)">Logo</a>
    <ul class="footer-contacts" v-if="contacts">
        <li v-for="(phone, index) in contacts.phones" :key="index" class="is-flex">
          <span><icon class="fa-phone" :name="phone.icon"></icon></span>
          <a :href="phone.href">{{ phone.view }}</a>
        </li>
        <li class="is-flex">
          <span><icon class="fa-phone" :name="contacts.email.icon"></icon></span>
          <a :href="contacts.email.href">{{ contacts.email.view }}</a>
        </li>
      </ul>
  </footer>
</template>
<script>
  import { mapGetters } from 'vuex'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/mobile-phone'
  import 'vue-awesome/icons/phone'
  import 'vue-awesome/icons/envelope'
  export default {
    data () {
      return {
      }
    },
    components: {
      Icon
    },
    computed: {
      ...mapGetters({
        contacts: 'getContacts'
      })
    }
  }
</script>
<style lang="scss" scoped>
@import '../../assets/variable.scss';
.footer{
  padding: 10px 15px;
  background: $headerColor;
  color: $color0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-contacts{
    font-size: 0.8rem;
    margin: 0 20px 0 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    span{
      display: inline-block;
      vertical-align: middle;
      width: 30px;
      text-align: center;
    }
  }
}
</style>



