<template>
  <div>
    <h2 class="title is-2">Продукты</h2>
    <section class="doubleColumn container">
      <div class="items blogItems">
      <product v-for="(product, index) in products" :product="product" :key="index"></product>
      </div>
    </section>
    <hr>
    <div>
      <h4 class="title is-4">Куда вам доставить?</h4>
      <form class="form-default columns is-multiline" @submit.prevent="sendCustomer">
        <div class="column is-one-third-desktop">
          {{ $t('form.delivery')}}
          <div class="select">
            <select v-model="customer.delivery">
              <option :value="{value:$t('delivery.personally.value'), title: $t('delivery.personally.title')}">{{ $t('delivery.personally.title')}}</option>
              <option :value="{value:$t('delivery.address.value'), title: $t('delivery.address.title')}">{{ $t('delivery.address.title')}}</option>
              <option :value="{value:$t('delivery.post.value'), title: $t('delivery.post.title')}">{{ $t('delivery.post.title')}}</option>
            </select>
          </div>
        </div>
        <div class="column is-one-third-desktop">
          {{ $t('form.address')}}
          <input class="input" type="text" v-model="customer.address">
        </div>
        <div class="column is-one-third-desktop">
          {{ $t('form.payment')}}
          <div class="select">
            <select v-model="customer.payment">
              <option :value="{value: $t('payment.cash.value'), title: $t('payment.cash.title')}">{{ $t('payment.cash.title')}}</option>
              <option :value="{value: $t('payment.nonCash.value'), title: $t('payment.nonCash.title')}">{{ $t('payment.nonCash.title')}}</option>
            </select>
          </div>
        </div>
        <div class="column">
          <button class="button is-success" type="submit">{{ $t('buttons.apply')}}</button>
        </div>
      </form>
    </div>
    <message></message>
  </div>
</template>

<script>
  import product from '@/components/View/Product'
  import cart from '@/components/View/Cart'
  import { mapGetters } from 'vuex'
  import message from '@/components/View/message'

  export default {
    name: 'Products',
    data () {
      return {
        customer: {
          delivery: {
            value: '',
            title: ''
          },
          address: '',
          payment: {
            value: '',
            title: ''
          }
        }
      }
    },
    components: {
      product,
      cart,
      message
    },
    computed: {
      ...mapGetters({
        itemsCart: 'getItemsCart',
        products: 'getProducts',
        customers: 'getCustomer'
      })
    },
    methods: {
      sendCustomer () {
        this.$store.dispatch('changeCustomer', {type: 'customer', value: this.customer})
      }
    },
    mounted () {
      Object.assign(this.customer, this.customers)
    }
  }
</script>

<style lang="scss">
</style>



