<template>
  <div class="order-form">
    <form @submit.prevent="sendForm(form, itemsCart)">
      <label class="label">
        {{ $t('form.name')}}
        <input class="input" v-model="form.name" type="text">
      </label>
      <label  class="label">
        {{ $t('form.email')}}
        <input class="input" v-model="form.email" type="email">
      </label>
      <label class="label label-select">
        {{ $t('form.payment')}}
        <div class="select">
        <select v-model="form.payment">
          <option :value="{value: $t('payment.cash.value'), title: $t('payment.cash.title')}">{{ $t('payment.cash.title')}}</option>
          <option :value="{value: $t('payment.nonCash.value'), title: $t('payment.nonCash.title')}">{{ $t('payment.nonCash.title')}}</option>
        </select>
        </div>
      </label>
      <label class="label label-select">
        {{ $t('form.delivery')}}
        <div class="select">
        <select v-model="form.delivery" >
          <option :value="{value:$t('delivery.personally.value'), title: $t('delivery.personally.title')}">{{ $t('delivery.personally.title')}}</option>
          <option :value="{value: $t('delivery.address.value'), title: $t('delivery.address.title')}">{{ $t('delivery.address.title')}}</option>
          <option :value="{value:$t('delivery.post.value'), title: $t('delivery.post.title')}">{{ $t('delivery.post.title')}}</option>
        </select>
        </div>
      </label>
      <label class="label">
        <span>{{ $t('form.address')}}</span>
        <input class="input" type="text" v-model="form.address">
      </label>
      <label class="label">
        {{ $t('form.additionText')}}
        <textarea class="textarea" v-model="form.text"></textarea>
      </label>
      <label class="label">
        {{ $t('form.recall')}}
        <input v-model="form.confirm" type="checkbox">
      </label>
      <button class="button is-link">{{ $t('buttons.send')}}</button>
    </form>

    <div class="cart-order" id="scrollBar">
      <strong>{{ $t('form.cart')}}</strong>
      <div  v-for="(item, index) in itemsCart" :key="index" class="card">
        <div class="card-image" >
          <figure class="image is-4by3">
            <img :src="item.simpleProducts[item.activeSimple].images[0].src" :alt="item.simpleProducts[item.activeSimple].images[0].alt">
          </figure>
        </div>
        <div class="card-content">
          <div class="media">
            <div class="media-content">
              <p class="title is-4">  {{item.title}}</p>
              <p class="subtitle is-6">{{item.simpleProducts[item.activeSimple].price}} {{ $t('buttons.price')}}</p>
            </div>
          </div>

          <div class="content is-6">
            {{ $t('form.count')}}: {{item.count}} {{ $t('buttons.count')}}
          </div>
          <a href="#" @click.prevent="deleteItem(index)">Удалить <icon name="trash"></icon></a>

        </div>
      </div>
    </div>

  </div>
</template>

<script>
  /* eslint-disable no-new */

  import { mapGetters } from 'vuex'
  import ApiShop from '@/api/shop'
  import Icon from 'vue-awesome/components/Icon'
  import PerfectScrollbar from 'perfect-scrollbar'

  export default {
    name: 'Order',
    data () {
      return {
        form: {
          name: '',
          email: '',
          payment: {
            value: '',
            title: ''
          },
          confirm: false,
          address: '',
          delivery: {
            value: '',
            title: ''
          },
          text: '',
          statusPayment: 'Не оплачено'
        }
      }
    },
    components: {
      Icon
    },
    computed: {
      ...mapGetters({
        itemsCart: 'getItemsCart',
        user: 'getUser',
        customers: 'getCustomer',
        products: 'getProducts'
      })
    },
    methods: {
      sendForm (form, itemsCart) {
        let arrItems = []
        for (let i = 0; i < itemsCart.length; i++) {
          arrItems.push({ superSku: itemsCart[i].superSku, sku: itemsCart[i].activeSimple, count: itemsCart[i].count })
        }
        let order = Object.assign(form, { items: arrItems })
        ApiShop.createOrder(order)
          .then(response => {
            this.$store.dispatch('orderCreated', response.data)
            this.$store.dispatch('cartClear')
            this.$router.push({path: 'typage'})
          })
          .catch(errors => {
            alert('Error')
          })
      },
      deleteItem (index) {
        this.$store.dispatch('deleteItemFromCart', this.itemsCart[index], index)
      }
    },
    created () {
      Object.assign(this.form, this.user, this.customers)
    },
    mounted () {
      new PerfectScrollbar('#scrollBar')
    }
  }
</script>

<style lang="scss">
  @import "~perfect-scrollbar/css/perfect-scrollbar";

  .label{
    max-width: 500px;
    margin:auto;
  }
  .label-select{
    display: flex;
    justify-content: space-between;
  }
  .cart-order{
    padding: 1rem;
    width:100%;
    max-width: 350px;
    margin: 2rem auto;
    border:1px solid #e6e6e6;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    max-height: 550px;
    overflow: auto;
    position: relative;
    &>div{
    }
  }
  .label.label-select{
    flex-direction: column;
  }
  .order-form{
    display: flex;
    flex-wrap: wrap;
    form{
      margin: auto;
    }
  }
</style>



