<template>
  <modal name="question" @before-open="beforeOpen">
    <div class="basic-modal">
      <h1 v-if="id" class="title">{{ $t('message.questionAbout')}}
            {{id.simpleProducts[id.activeSimple].title}}
      </h1>
      <form @submit.prevent="sendForm(form, id.activeSimple)">
        <label for="name">
          {{ $t('form.name')}}
          <input class="input" v-model="form.name" type="text" id="name">
        </label>
        <label for="email">
          {{ $t('form.email')}}
          <input class="input" v-model="form.email" type="text" id="email">
        </label>
        <label for="area">
          Введите ваш вопрос
          <textarea class="textarea" v-model="form.text" name="area" id="area"></textarea>
        </label>
        <label for="checkbox" class="label-checkbox">
          <span>{{ $t('form.sure')}}</span>
          <input v-model="form.checkbox" type="checkbox" id="checkbox">
        </label>
        <button class="button is-success is-outlined" type="submit">{{ $t('buttons.send')}}</button>
      </form>
      <button class="button button-close-modal" type="button" @click="close">
        <icon name="close"></icon>
      </button>
    </div>
  </modal>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Api from '@/api/shop'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/close'

  export default {
    name: 'modalQuestion',
    data () {
      return {
        form: {
          name: '',
          email: '',
          checkbox: false,
          text: ''
        },
        id: ''
      }
    },
    components: {
      Icon
    },
    methods: {
      beforeOpen (event) {
        this.id = event.params.id
        return event.params.id
      },
      close () {
        this.$modal.hide('question')
      },
      sendForm (value, sku) {
        let form = Object.assign(value, {sku})
        Api.sendForm(form)
          .then(response => {
            this.$store.dispatch('sendStatus', {status: true})
            this.resetForm()
          })
          .catch(errors => {
            this.$store.dispatch('sendStatus', {status: false})
          })
      },
      resetForm () {
        this.form = {
          name: '',
          email: '',
          checkbox: false,
          text: ''
        }
      }
    },
    computed: {
      ...mapGetters({
        products: 'getProducts'
      })
    }
  }
</script>

<style lang="scss">
  .basic-modal {
    padding: .5rem;
    form{
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }
    label{
      textarea{
        width: 100%;
        resize: none;
      }
    }
    .title{
      margin-bottom: .5rem;
    }
  }
  .button-close-modal{
    position: absolute;
    right:5px;
    top:10px;
  }
  .label-checkbox{
    input{
      width: initial;
    }
  }
  [data-modal="question"]{
    .v--modal{
      height: 430px !important;
    }
  }
  .label-checkbox {
    margin: 0.5rem 0 0 0;
  }
</style>



