<template>
  <div>
      <h2 class="title is-2">Blog</h2>
      <ul class="blog__wrap">
        <li v-for="blogPost in blog" :key="blogPost.id">
          <h5 class="title is-5">
            {{ blogPost.title }}
            <strong class="is-size-7 has-text-grey-light">{{ blogPost.date }}</strong>
          </h5>
          <p>{{ blogPost.description }}
            <router-link :to="{name: 'post', params: {id: blogPost.id}}">...more&raquo;</router-link></p>
        </li>
      </ul>
      <pagination
        :amountPages="paginationPageLength"
        :paginationLinks="3"
        :currentPage="currentPage"
        @returnNewCurrent="getNewPage">
      </pagination>
  </div>
</template>
<script>
  import BlogApi from '@/api/blog'
  import pagination from '@/components/View/pagination'
  export default {
    name: 'blog',
    components: {
      pagination
    },
    data () {
      return {
        blog: [],
        currentPage: 2,
        blogLength: 0,
        numberOfPosts: 2
      }
    },
    created () {
      this.getPage(this.numberOfPosts, this.currentPage)
    },
    computed: {
      paginationPageLength () {
        return Math.ceil(this.blogLength / this.numberOfPosts)
      }
    },
    methods: {
      getPage (num, id) {
        BlogApi.getBlog(num, id)
        .then(blog => {
          this.blogLength = parseInt(blog.headers['x-total-count'])
          this.blog = blog.data
        })
        .catch(errors => {
          console.log(errors)
        })
      },
      getNewPage (page) {
        this.currentPage = page
        this.getPage(this.numberOfPosts, this.currentPage)
      }
    }
  }
</script>
<style lang="scss" scoped>
  .blog{
    &__wrap{
      list-style: none;
      text-align: left;
      li{
        margin: 0 0 30px;
      }
      h5{
        margin: 0 0 10px;
      }
    }
  }
</style>



