const state = {
  blog: [],
  currentPage: 1
}

const getters = {
  getBlog: state => state.blog,
  getBlogCurrent: state => state.currentPage
}

const actions = {
  addBlog: function ({commit}, value) {
    commit('SET_BLOG', value)
  },
  setBlogCurrent: function ({commit}, value) {
    console.log('setBlogCurrent')
    commit('SET_BLOG_CURRENT', value)
  }
}

const mutations = {
  SET_BLOG: function (state, blog) {
    state.blog = blog
  },
  SET_BLOG_CURRENT: function (state, currentPage) {
    state.currentPage = currentPage
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



