<template>
  <div class="paginator">
    <div>
      move to <!-- multiple :size="amountPages" -->
      <select v-model.number="selected">
        <option value="0" disabled>...</option>
        <option v-for="option in amountPages" :key="option" :value="option">
          {{ option }}
        </option>
      </select>
    </div>
    <ul class="paginator__list">
      <li @click="newCurrent = localCurrent - 1" :class="{ disabled: startPagination < 2 }" class="paginator__arrow arrowLeft">&lsaquo;</li>
      <li v-for="link in links" 
      :key="link" :class="{ current: localCurrent === link }"
      @click="newCurrent = link"
      class="paginator__link">{{ link }}</li>
      <li @click="newCurrent = localCurrent + 1" :class="{ disabled: endPagination === amountPages }" class="paginator__arrow arrowRight">&rsaquo;</li>
    </ul>
    <div>{{ localCurrent }} from {{ amountPages }}</div>
  </div>
</template>
<script>
  export default {
    props: ['amountPages', 'currentPage', 'paginationLinks'],
    data () {
      return {
        newCurrent: 0,
        default: {
          paginationLinks: 5,
          currentPage: 1
        },
        pagination: {},
        selected: 0
      }
    },
    created () {
      this.pagination.paginationLinks = this.paginationLinks || this.default.paginationLinks
    },
    computed: {
      localCurrent () {
        let current = this.currentPage || this.default.currentPage
        return current
      },
      centerPagination () {
        return Math.floor(this.pagination.paginationLinks / 2)
      },
      startPagination () {
        if ((this.localCurrent - this.centerPagination) < 1) {
          return 1
        } else if (((this.localCurrent - this.centerPagination) > 0) && ((this.localCurrent - this.centerPagination) < (this.amountPages - this.pagination.paginationLinks + 1))) {
          return (this.localCurrent - this.centerPagination)
        } else {
          return (this.amountPages - this.pagination.paginationLinks + 1)
        }
      },
      endPagination () {
        return (this.startPagination + this.pagination.paginationLinks - 1)
      },
      links () {
        let $this = this
        let start = $this.startPagination
        let arr = []
        for (let i = 0; i < $this.pagination.paginationLinks; i++) {
          arr.push(start + i)
        }
        return arr
      }
    },
    watch: {
      newCurrent () {
        if (this.newCurrent !== this.localCurrent) {
          this.$emit('returnNewCurrent', this.newCurrent)
        }
      },
      selected (val) {
        if (val !== 0) {
          this.selected = 0
          this.newCurrent = val
        }
      }
    },
    methods: {
    }
  }
</script>
<style lang="scss" scoped>
  .paginator{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin: 20px 0;
    &__list{
      list-style: none;
      li{
        display: inline-block;
        vertical-align: top;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        margin: 0 2px;
        cursor: pointer;
        &.disabled{
          visibility: hidden;
        }
      }
    }
    &__link{
      border-radius: 3px;
      background: #dcd8d8;
      &:hover{background: #ccc;}
      &.current{
        background: #f00;
        color: #fff;
      }
    }
    &__arrow{
      background: transparent;
      font-size: 37px;
      &:hover{color: #000;}
    }
  }
</style>


