<template>
  <div>
    <div class="articles">
      <h2 v-if="post.length">Статьи продукта: </h2>
      <div v-else>К сожалению, у этого товара нет  статей</div>
      <template>
        <div class="columns is-multiline is-mobile">
          <div v-for="(item, index) in post" :key="index">
            {{item.title}}
            <br>
            {{item.description}}
            <router-link :to="{name: 'post', params: {id: item.id}}">...more&raquo;</router-link></p>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
  import BlogApi from '@/api/blog'
  import product from '@/components/View/Product'
  import { mapGetters } from 'vuex'
  export default {
    name: 'articleProducts',
    data () {
      return {
        post: {}
      }
    },
    components: {
      product
    },
    computed: {
      ...mapGetters({
        products: 'getProducts'
      })
    },
    created () {
      let sku = (this.$route.params.id).split('_')[1]
      BlogApi.getPostBySku(sku)
        .then(post => {
          console.log(post)
          this.post = post.data
        })
        .catch(errors => {
          console.log(errors)
        })
    }
  }
</script>



