import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Index from '@/components/Index.vue'
import Order from '@/components/Order.vue'
import TYpage from '@/components/typage.vue'
import Blog from '@/components/Blog.vue'
import Post from '@/components/Post.vue'
import ArticleProducts from '@/components/articleProducts.vue'

export default new Router({
  routes: [
    { path: '/', component: Index },
    { path: '/order', component: Order },
    { path: '/typage', component: TYpage },
    { path: '/blog', component: Blog },
    { name: 'post', path: '/blog/:id', component: Post },
    { name: 'articlesProduct', path: '/articles/:id', component: ArticleProducts }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



