<template>
  <div>
    <section class="homeTop" :style="{'background-image': 'url(' + require('@/assets/tehnology.jpg') + ')'}">
      <div class="container">
        <h1 class="title is-1">Лендинг-магазин</h1>
        <p class="subtitle">Самый легкий и быстрый! Моментальная загрузка!</p>
      </div>
    </section>
    <div class="container">
      <products></products>
    </div>
  </div>
</template>

<script>
  import Products from '@/components/Products'
  import ApiMain from '@/api/main'

  export default {
    name: 'Index',
    components: {
      Products
    },
    created () {
      ApiMain.getProducts()
      .then(products => {
        this.$store.dispatch('addProducts', products.data)
      })
      .catch(errors => {
        alert(errors)
      })
    }
  }
</script>
<style lang="scss" scoped>
  @import '../assets/variable.scss';
  .homeTop{
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: $color0;
    text-align: left;
        padding: 14% 0;
  }
  .title, .subtitle{
    color: inherit;
  }
</style>



