<template>
  <div class="post">
    <div class="breadcrumb">
      <router-link to="/blog">&#8592; Blog</router-link>
    </div>
    <h2>{{ post.title }}</h2>
    <div class="post__article">{{ post.article }}</div>
    <template v-if="products">
      <h3>Продукты</h3>
      <div class="columns is-multiline is-mobile">
        <product v-for="(product, index) in products" :product="product" :num="index" :key="index"></product>
      </div>
    </template>
    <div v-else>К сожалению, у этой статьи нет сопутствуюших товаров</div>
  </div>
</template>
<script>
  import BlogApi from '@/api/blog'
  import product from '@/components/View/Product'
  import { mapGetters } from 'vuex'

  export default {
    components: {
      product
    },
    data () {
      return {
        post: {},
        articleProducts: []
      }
    },
    computed: {
      ...mapGetters({
        products: 'getProducts'
      })
    },
    created () {
      BlogApi.getPost(this.$route.params.id)
      .then(post => {
        this.post = post.data

        if (this.post.productsOfArticle.length) {
          BlogApi.getPostProducts()
            .then(response => {
//              this.products = response.data.find(product => this.post.productsOfArticle.includes(product.superSKU))
              for (let item of this.post.productsOfArticle) {
                let a = response.data.find((x) => x.superSku === item)
                this.articleProducts.push(a)
              }
              this.$store.dispatch('addArticleProducts', {product: this.articleProducts})
            })
            .catch(errors => {
              console.log(errors)
            })
        }
      })
      .catch(errors => {
        console.log(errors)
      })
    }
  }
</script>
<style lang="scss" scoped>
  .breadcrumb, .post__article{
    text-align: left;
  }
</style>




